<template>
  <div>
    <mrc_ess_iconGroup :is_disabled="disabled"
                       @handleReturn='handleReturn'
                       @handleEdit='handleEdit'
                       @handleDel='handleDel'></mrc_ess_iconGroup>
    <v-tabs color='primary'
            v-model="tab"
            grow>
      <v-tab v-for="item in tablist"
             :key="item.name">
        {{ item.name }}
      </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item>
        <keep-alive>
          <component ref="baseSet"
                     :is="baseSet"
                     :loading='loading'
                     :disabled='disabled'
                     :propsParams='baseInfo'
                     @success='success1'
                     @cancel='cancel1'
                     @change='change'></component>
        </keep-alive>
      </v-tab-item>
      <v-tab-item>
        <keep-alive>
          <component ref="funcSet"
                     :is="funcSet"
                     :loading='loading'
                     @success='success1'
                     @cancel='cancel1'
                     :disabled='disabled'
                     :propsParams='permission'></component>
        </keep-alive>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
import baseSet from '../components/baseSet'
import funcSet from '../components/funcSet'
import { role_info, role_update, role_del } from '@/service/api/role'
import moment from 'moment'
export default {
  components: {},
  props: {},
  data () {
    return {
      loading: false,
      disabled: true,
      tab: 0,
      baseSet: baseSet,
      funcSet: funcSet,
      baseInfo: {},
      permission: [],
      newPermission: []
    };
  },
  computed: {
    tablist () {
      return [
        { name: this.$t('label_base_set'), component: baseSet, params: this.baseInfo },
        { name: this.$t('label_Functional_authority'), component: funcSet, params: this.permission }
      ]
    },
  },
  created () { },
  mounted () {
    if (!this.$store.state.iconstatus) {
      this.disabled = false
    }
    this.tab = this.$store.state.tab
    this.$route.meta.title = this.$store.getters.subPage_name
    this.getData()
  },
  methods: {
    change (val) {
      this.baseInfo = val
    },
    async success1 (val) {
      await this.success()
    },
    cancel1 () {
      this.$myConfirm({ content: this.$t('message_Confirm_the_cancellation_of_the_edit') })
        .then(async () => {
          await this.getData()
          this.disabled = !this.disabled
        }).catch(() => { })
    },
    async getData () {
      const info = await role_info({ uuid: this.$store.state.uuid })
      if (info.code == 200) {
        const {
          uuid,
          role_name,
          role_description,
          is_used,
          org_level_uuid,
          sequence,
          effective_start_date,
          effective_end_date,
          userNum
        } = info.data
        this.permission = info.mrbase_sys_function_permission ? info.mrbase_sys_function_permission : []
        this.baseInfo = {
          uuid,
          role_name,
          role_description,
          is_used,
          org_level_uuid,
          sequence,
          effective_start_date,
          effective_end_date,
          userNum
        }
      }
    },
    cancel () {
      this.$myConfirm({ content: this.$t('message_Confirm_the_cancellation_of_the_edit') })
        .then(async () => {
          await this.getData()
          this.disabled = !this.disabled
        }).catch(() => { })

    },
    async success () {
      // console.log(this.$refs.funcSet.eleTree());
      // return
      // 先判断 基本数据是否都符合校验
      this.$refs.baseSet.$v.params.$touch()
      if (this.$refs.baseSet.$v.params.$error) return
      // this.$refs.funcSet.eleTree() 这是选中的数据
      let permission
      try {
        permission = this.$refs.funcSet.eleTree()
      } catch (error) {
        permission = this.permission
      }

      const obj = Object.assign(
        {},
        this.baseInfo,
        {
          mrbase_sys_function_permission: permission,
          effective_start_date: moment(this.baseInfo.effective_start_date).format('YYYY-MM-DD'),
          effective_end_date: moment(this.baseInfo.effective_end_date).format('YYYY-MM-DD')
        })
      this.loading = true
      const update = await role_update(obj)
      if (update.code == 200) {
        this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
        this.disabled = !this.disabled
      }
      this.loading = false
    },
    handleReturn () {
      this.$router.go(-1)
    },
    handleEdit () {
      this.disabled = !this.disabled
    },
    // 删除
    handleDel () {
      this.$myConfirm({ title: '提示', content: '是否删除' })
        .then(async () => {
          const del = await role_del({
            uuid: this.$store.state.uuid
          })
          if (del.code == 200) {
            this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
            this.$router.go(-1)
          }
        }).catch(() => { })
    }
  },
  watch: {},
};
</script>

